<template>
  <el-form :model="form" label-width="80px" ref="form">
    <el-form-item :model="form" label-width="80px" ref="form">
      <el-form-item label="标题">
        <el-input v-model="form.title"></el-input>
      </el-form-item>
    </el-form-item>

    <el-form-item :model="form" label-width="80px" ref="form">
      <el-form-item label="栏目">
        <el-checkbox-group v-model="form.categories">
          <el-checkbox
            :key="index"
            :label="item.id"
            v-for="(item,index) in categoryList"
          >{{item.name}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
    </el-form-item>

    <el-form-item :model="form" label-width="80px" ref="form">
      <el-form-item label="类型">
        <el-radio label="1" v-model="form.radio">文章</el-radio>
        <el-radio label="2" v-model="form.radio">视频</el-radio>
      </el-form-item>
    </el-form-item>

    <el-form-item style="margin-left: 40px;">
      <el-button @click="onSubmit" type="primary">提交</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      form: {
        title: '',
        categories: [],
        radio: '1'
      },
      categoryList: []
    }
  },
  methods: {
    onSubmit() {
      console.log(this.form)
    }
  },
  mounted() {
    this.$axios({
      url: '/category',
      method: 'get'
    }).then(res => {
      const { data } = res.data
      console.log(data)
      this.categoryList = data
    })
  }
}
</script>

<style lang="less" scoped>
</style>


